<template>
    <div>

        <AppHeader />



        <div id="noticeReadContentAll">

            <p id="noticeReadTitle">{{ noticeVo.title }}</p>

            <div id="noticeReadTopBottom">
                <div id="leftInformation">
                    <span id="noticeWriter"></span>
                    <span id="noticeReadWriter">{{ noticeVo.uname }}</span>
                </div>

                <div id="rightInformation">
                    <span id="noticeRegDate"></span>
                    <span id="noticeReadRegDate">{{ noticeVo.regDate }}</span>
                </div>
            </div>

            <p id="noticeReadContent">{{ noticeVo.content }}</p>

            <div id="btnGroup">
                <button id="btnGoToList" onclick="location.href='/announcements'"></button>
                <button id="btnDelete" @click="deleteNotice(noticeVo.no)"></button>
                <button id="btnModify" @click="goToModifyPage(noticeVo.no)"></button>
            </div>

        </div>



        <AppFooter />

    </div>
</template>


<script>
import AppFooter from '@/components/AppFooter.vue';
import AppHeader from '@/components/AppHeader.vue';
import '@/assets/css/notice/NoticeReadView.css';
import axios from 'axios';

export default {
    name: "NoticeReadView",
    components: {
        AppFooter,
        AppHeader
    },
    data() {
        return {
            noticeVo:{
                no:this.$route.params.no,
                title:"",
                uname:"",
                regDate:"",
                content:""
            }
        };
    },
    methods: {
        readOneNotice(){
            console.log("  ");

            axios({
                method: 'get', // put, post, delete                   
                url: `${this.$store.state.apiBaseUrl}/api/notice/read/`+this.noticeVo.no,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                //params: {uName:this.noticeVo.uName}, //get   
                //data: guestbookVo, //put, post, delete   JSON  

                responseType: 'json' //
            }).then(response => {
                console.log(response); //

                this.noticeVo=response.data.apiData;

            }).catch(error => {
                console.log(error);
            });
        },
        deleteNotice(no){
            console.log("  ");

            axios({
                method: 'delete', // put, post, delete                   
                url: `${this.$store.state.apiBaseUrl}/api/notice/delete/`+no,
                headers: { "Content-Type": "application/json; charset=utf-8" }, //
                //params: guestbookVo, //get   
                data: {no}, //put, post, delete   JSON  

                responseType: 'json' //
            }).then(response => {
                console.log(response.data); //

                console.log(" ");

                location.href="/announcements"

            }).catch(error => {
                console.log(error);
            });
        },
        goToModifyPage(no){
            console.log(" ");

            location.href="/noticeModify/"+no;
        }
    },
    created() {
        this.readOneNotice();
    }
};
</script>

<style></style>